<template>
  <h3>Search for posts</h3>
  <my-input
      id="searchBar"
      v-model="searchQuery"
      placeholder="What would you like to search for?"
  />
  <div class="app_btns">
    <my-button @click="showDialog">Create a Post</my-button>
    <my-select v-model="selectedSort" />
  </div>
  <!-- <my-button @click="fetchPosts">Try to get All Posts</my-button> --->

  <my-dialog v-model:show="dialogVisible"
  ><post-form @create="createPost"
  /></my-dialog>
  <post-list :posts="searchPosts" @remove="removePost" v-if="!isPostsLoading" />
  <div v-else>LOADING...</div>
  <!-- Page wrapper responsible in limiting 10 posts per page --->
  <div class="page_wrapper">
    <div
        v-for="pageNumber in totalPages"
        :key="pageNumber"
        class="page"
        :class="{ 'current-page': page === pageNumber }"
        @click="changePage(pageNumber)"
    >
      {{ pageNumber }}
    </div>
  </div>
  <!--<div ref="observer" class="observer">Fetching new posts...</div> -->
</template>

<script>
import PostForm from "@/components/PostForm";
import PostList from "@/components/PostList";
import MyDialog from "@/components/UI/MyDialog";
import MyButton from "@/components/UI/MyButton";
import axios from "axios";
import MySelect from "@/components/UI/MySelect";
import MyInput from "@/components/UI/MyInput";

export default {
  components: {
    MyInput,
    MySelect,
    MyButton,
    MyDialog,
    // eslint-disable-next-line vue/no-unused-components
    PostList,
    // eslint-disable-next-line vue/no-unused-components
    PostForm,
  },
  data() {
    return {
      posts: [],
      /*posts: [
        { id: 1, title: "Javascript", body: "about post" },
        { id: 2, title: "Javascript2", body: "about post2" },
        { id: 3, title: "Javascript3", body: "about post3" },
      ],*/
      dialogVisible: false,
      isPostsLoading: false,
      selectedSort: "",
      searchQuery: "",
      page: 1,
      limit: 10,
      totalPages: 0,
      sortOptions: [
        { value: "title", name: "by name" },
        { value: "body", name: "by name" },
      ],
    };
  },

  methods: {
    createPost(post) {
      this.posts.push(post);
      this.dialogVisible = false;
    },
    removePost(post) {
      this.posts = this.posts.filter((p) => p.id !== post.id);
    },
    showDialog() {
      this.dialogVisible = true;
    },
    // shows limited amount of Posts (e.g. 10 posts) in each Page
    changePage(pageNumber) {
      this.page = pageNumber;
      this.fetchPosts();
    },
    async fetchPosts() {
      try {
        this.isPostsLoading = true;
        const response = await axios.get(
            "https://jsonplaceholder.typicode.com/posts",
            {
              params: {
                _page: this.page,
                _limit: this.limit,
              },
            }
        );
        this.totalPages = Math.ceil(
            response.headers["x-total-count"] / this.limit
        );
        this.posts = response.data;
        this.isPostsLoading = false;
      } catch (e) {
        alert("FAIL");
        // eslint-disable-next-line no-empty
      } finally {
      }
    },
    // creating dynamic post loading feed. Check "Intersection Observer API"
    /*  async loadMorePosts() {
      try {
        this.page += 1;
        //this.isPostsLoading = true;
        const response = await axios.get(
          "https://jsonplaceholder.typicode.com/posts",
          {
            params: {
              _page: this.page,
              _limit: this.limit,
            },
          }
        );
        this.totalPages = Math.ceil(
          response.headers["x-total-count"] / this.limit
        );
        this.posts = [...this.posts, ...response.data];
        this.isPostsLoading = false;
      } catch (e) {
        alert("FAIL");
        // eslint-disable-next-line no-empty
      } /*finally {
      }*/
    //},
  },

  mounted() {
    this.fetchPosts();
    /*const options = {
      rootMargin: "0px",
      threshold: 1.0,
    };
    const callback = (entries) => {
      if (entries[0].isIntersecting && this.page < this.totalPages) {
        this.loadMorePosts();
      }
    };
    const observer = new IntersectionObserver(callback, options);
    observer.observe(this.$refs.observer);*/
  },
  computed: {
    searchPosts() {
      return this.posts.filter((post) =>
          post.title.toLowerCase().includes(this.searchQuery.toLowerCase())
      );
    },
  },
};
</script>

