<template>
  <h1>Welcome to the Forum!</h1>
  <form
      @submit.prevent
      @click.prevent
  >
    <br/>
    <br/>
    <h3>Please Log In</h3>
    <p v-if="errors.length">
      <b>Please correct the following error(s):</b>
      <ul>
       <li :key="error" class="error" v-for="error in errors">{{ error }}</li>
      </ul>
    </p>

    <label for="credential">Username / Email</label>
    <my-input
        id="credential"
        v-model="login.credential"
        type="text"
        placeholder="Enter your username or email"
        required
    />

    <label for="password">Password</label>
    <my-input
        id="password"
        v-model="login.password"
        type="password"
        placeholder="Enter your password"
        required
    />

    <br/>

    <div class="buttons">

    <my-button class="btn" value="Cancel" @click="reloadPage">Cancel</my-button>
    <!--<my-button class="btn" @click="sendMessage">Submit</my-button>-->
    <!--  <my-button class="btn" value="Send" @click="createPost">Submit</my-button> -->
      <my-button class="btn" value="Send" @click="SendLogInUser">Log In</my-button>


    </div>
    <div>
    <strong>Not a Registered Member? </strong>
    <router-link to="/signup">Register</router-link> here
  </div>
  </form>
  <br /><br />

</template>

<script>
import MyInput from "@/components/UI/MyInput";
import MyButton from "@/components/UI/MyButton";
import axios from "axios";
import store from "@/store";

export default {
  components: { MyInput, MyButton },
  name: "LogIn",
  data() {
    return {
      login: {
        credential: "",
        password: "",
      },
      errors: [],
    };
  },
  methods: {
    checkForm: function () {

      this.errors = [];
      if (this.login.credential==="") {
        this.errors.push('Username or email required.');
      }
      if (this.login.password==="") {
        this.errors.push('Password required.');
      }
    },
    reloadPage() {
      window.location.reload();
    },
    SendLogInUser() {
      this.checkForm(this.login)
      if (this.errors.length < 1) {
        axios({
          method: "post",
          url: "http://localhost:9200/",
          headers: {"content-type": "text/plain"},
          data: this.login,
        }).then((result) => {
          console.log(result)
          //data we receive from database
          this.name = result.data["username"];
          store.state.user.username = result.data["username"]
          this.msg = result.data["msg"];
          this.flag = result.data["check"];
          // value is session
          this.value = result.data["value"];

          this.maxage = result.data["maxage"];
          console.log('received data', this)
          if (this.flag === true) {
            document.cookie =
                "session=" +
                this.value +
                ";" +
                "max-age=" +
                this.maxage +
                ";path=/posts";
            console.log("Data Sent ", result.data);
            alert(result.data["msg"]);
            setTimeout(() => this.$router.push({path: "/posts"}), 2000);
          } else {
            // this.msg = result.data["Username is taken"];
            alert(result.data["msg"]);
            location.reload()
          }
          console.log(this.login);
        });
      }

    },
  },
};
</script>

